import { Switch, Route, useRouteMatch } from "react-router-dom";
import { urlResolve } from "./utils";

export const Footer = () => {
  let match = useRouteMatch();
  console.log(match.path);
  return (
    <div>
      <div>Foooter</div>
      <Switch>
        <Route path={urlResolve(match.path, "blog")}>si si blog</Route>
        <Route path={urlResolve(match.path, "about")}>about</Route>
      </Switch>
    </div>
  );
};

export default Footer;
